// @ts-nocheck
import React, { useEffect, useState, useContext } from 'react';
import { Card, Button } from "@material-ui/core";
import { FirebaseContext } from "../firebaseContext";

export const ProfileList = () => {
  const [data, setData] = useState();
  const { getAllProfiles } = useContext(FirebaseContext);

  useEffect(() => {
    let allWallets;
    async function grab() {
      let res = await getAllProfiles();
      console.log(res);
      allWallets = res;
    }
    grab();

    async function fetchGraphQL() {
      let result = await fetch("https://api2.objkt.com/v1/graphql", {
        method: 'POST',
        body: JSON.stringify({
          query: getEventsLive,
          variables: {
            "order_by": [
              {
                "timestamp": "desc"
              },
              {
                "id": "desc"
              }
            ],
            "limit": 200,
            "offset": 0,
            "where": {
              "timestamp": {
                "_is_null": false
              },
              "event_type": {
                "_neq": "transfer"
              }
            }
          },
          operationName: "getEventsLive",
        }),
        headers: {
          "Content-Type": "application/json"
        }
      })
      var ress = await result.json();
      return ress;
    }

    async function fetchLiveEvents() {
      const { errors, data } = await fetchGraphQL()

      if (errors) {
        console.log(errors)
      }
      if (data) {
        console.log(data)
        const wallets = (data.event).map((el) => el.creator.address);
        console.log(wallets)
        setData(allWallets.concat(wallets));
      }
    }
    fetchLiveEvents()

  }, [getAllProfiles]);

  return (
    <Card style={{ height: "90vh", overflow: "auto" }}>

      <Button
        size={"large"}
        title={"Return Back"}
        onClick={() => {
          window.open(`https://assemblr.xyz/`, "_blank")
        }}
      >
        <u>Return Back</u>
      </Button>

      {data && data.map((address) => (
        address &&
        <div>
          <Button
            size={"large"}
            title={address}
            onClick={() => {
              window.open(`https://assemblr.xyz/?profile=${address}`, "_blank")

            }}
          >
            <u>{address}</u>
          </Button>
        </div>
      ))
      }


    </Card>
  );
}

const getEventsLive = `
query getEventsLive($where: event_bool_exp!, $order_by: [event_order_by!] = {}, $limit: Int = 100, $offset: Int = 0) {
  event(where: $where, order_by: $order_by, limit: $limit, offset: $offset) {
    ...EventLive
    __typename
  }
}

fragment EventLive on event {
  id
  amount
  price
  event_type
  timestamp
  token_pk
  fa_contract
  ophash
  fa {
    ...FaMinimal
    __typename
  }
  token {
    ...TokenLive
    __typename
  }
  creator {
    ...UserLight
    __typename
  }
  recipient {
    ...UserLight
    __typename
  }
  __typename
}

fragment FaMinimal on fa {
  contract
  name
  path
  collection_type
  collection_id
  __typename
}

fragment TokenLive on token {
  pk
  token_id
  artifact_uri
  display_uri
  thumbnail_uri
  fa_contract
  mime
  __typename
}

fragment UserLight on holder {
  address
  alias
  logo
  __typename
}


  `
